$(function () {
  /*- Tabs --------------
  - ÐšÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ñƒ ÐºÐ½Ð¾Ð¿Ð¾Ðº Ð·Ð°Ð´Ð°ÐµÐ¼ ÐºÐ»Ð°ÑÑ j-tabs
  Ð¸ ÑƒÐºÐ°Ð·Ñ‹Ð²Ð°ÐµÐ¼ Ñ†ÐµÐ»ÐµÐ²Ð¾Ð¹ ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€ Ð² Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ðµ data-target

  - ÐšÐ½Ð¾Ð¿ÐºÐ°Ð¼ Ð·Ð°Ð´Ð°ÐµÐ¼ ÐºÐ»Ð°ÑÑ j-tab-item

  - ÐšÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ñƒ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚Ð½Ñ‹Ñ… Ð±Ð»Ð¾ÐºÐ¾Ð² j-tabs-content-container Ð¸ Ð¸Ð´ÐµÐ½Ñ‚Ð¸Ñ„Ð¸ÐºÐ°Ñ‚Ð¾Ñ€ data-tabs-content

  - ÐšÐ¾Ð½Ñ‚ÐµÐ½Ñ‚Ð½Ñ‹Ð¼ Ð±Ð»Ð¾ÐºÐ°Ð¼ j-tab-content

  - ÐÐºÑ‚Ð¸Ð²Ð½Ð°Ñ Ð²ÐºÐ»Ð°Ð´ÐºÐ° Ð´Ð¾Ð»Ð¶Ð½Ð° Ð¸Ð¼ÐµÑ‚ÑŒ ÐºÐ»Ð°ÑÑ is-active

  ÐŸÐ¾Ñ€ÑÐ´Ð¾Ðº ÐšÐ½Ð¾Ð¿Ð¾Ðº Ð¾Ñ‚Ð½Ð¾ÑÐ¸Ñ‚ÐµÐ»ÑŒÐ½Ð¾ ÐšÐ¾Ð½Ñ‚ÐµÐ½Ñ‚Ð½Ñ‹Ñ… Ð±Ð»Ð¾Ð»ÐºÐ¾Ð² Ð¾Ð¿Ñ€ÐµÐ´ÐµÐ»ÑÐµÑ‚ÑÑ Ð°Ð²Ñ‚Ð¾Ð¼Ð°Ñ‚Ð¸Ñ‡ÐµÑÐºÐ¸, Ð¸Ð»Ð¸ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð¾Ð¼ data-tab-index Ñƒ ÐºÐ½Ð¾Ð¿Ð¾Ðº(Ð¾Ñ‚ÑÑ‡ÐµÑ‚ Ñ 0) / ----*/

  const intitTabs = () => {
    const $allTabs = $('.j-tabs');

    $allTabs.each((i, element) => {
      const $tab = $(element).find('.j-tab-item');

      if ($tab.hasClass('is-active')) {
        const context = $(element).attr('data-target');
        const isSetDataIndex = $(element).find('[data-tab-index]').length;
        let index = undefined;

        if (isSetDataIndex) {
          index = $tab.closest('.is-active').attr('data-tab-index');
        } else {
          index = $tab.closest('.is-active').index();
        }

        const $contentContainer = $(`[data-tabs-content='${context}'`);
        const $contentItem = $contentContainer.find('.j-tab-content');

        //- Ð—Ð°Ð´Ð°ÐµÐ¼ ÐºÐ»Ð°ÑÑ is-active Ð´Ð»Ñ Ð²ÐºÐ»Ð°Ð´ÐºÐ¸ ÑÐ¾Ð¾Ñ‚Ð²ÐµÑ‚ÑÑ‚Ð²ÑƒÑŽÑ‰ÐµÐ¹ Ð°ÐºÑ‚Ð¸Ð²Ð½Ð¾Ð¹ ÐºÐ½Ð¾Ð¿ÐºÐµ
        //- Ð—Ð°Ð´Ð°ÐµÐ¼ Ð²Ñ‹ÑÐ¾Ñ‚Ñƒ ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ð° Ñ€Ð°Ð²Ð½ÑƒÑŽ Ð²Ñ‹ÑÐ¾Ñ‚Ðµ Ð°ÐºÑ‚Ð¸Ð²Ð½Ð¾Ð¹ Ð²ÐºÐ»Ð°Ð´ÐºÐ¸
        $contentContainer.height($contentItem.eq(index).addClass('is-active').innerHeight());
      }
    });
  };

  intitTabs();


  const $tab = $('.j-tab-item');

  $tab.click(function (e) {
    const $tabs = $(this).closest('.j-tabs');
    const context = $tabs.attr('data-target');
    const $contentContainer = $(`[data-tabs-content='${context}'`);
    const $contentItem = $contentContainer.find('.j-tab-content');

    const isSetDataIndex = $tabs.find('[data-tab-index]').length;
    let index = undefined;

    if (isSetDataIndex) {
      index = $(this).attr('data-tab-index');
    } else {
      index = $(this).index();
    }

    $tab.removeClass('is-active');
    $(this).addClass('is-active');

    $contentItem.removeClass('is-active');
    //- Ð—Ð°Ð´Ð°ÐµÐ¼ ÐºÐ»Ð°ÑÑ is-active Ð´Ð»Ñ Ð²ÐºÐ»Ð°Ð´ÐºÐ¸ ÑÐ¾Ð¾Ñ‚Ð²ÐµÑ‚ÑÑ‚Ð²ÑƒÑŽÑ‰ÐµÐ¹ Ð°ÐºÑ‚Ð¸Ð²Ð½Ð¾Ð¹ ÐºÐ½Ð¾Ð¿ÐºÐµ
    //- Ð—Ð°Ð´Ð°ÐµÐ¼ Ð²Ñ‹ÑÐ¾Ñ‚Ñƒ ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ð° Ñ€Ð°Ð²Ð½ÑƒÑŽ Ð²Ñ‹ÑÐ¾Ñ‚Ðµ Ð°ÐºÑ‚Ð¸Ð²Ð½Ð¾Ð¹ Ð²ÐºÐ»Ð°Ð´ÐºÐ¸
    $contentContainer.height($contentItem.eq(index).addClass('is-active').innerHeight());
  });
});
